import React from "react";

const Check = () => {
  return (
    <div className="container" style={{ marginTop: "10%", marginLeft: "38%" }}>
      <img src="data:image/png;base64," />
      <a
        style={{
          display: "block",
          paddingLeft: "10%",
          fontSize: "20px",
          marginTop: "2%",
        }}
      >
        Cart is empty!
      </a>
      <a style={{ display: "block", paddingLeft: "7%" }}>
        explore the items you like
      </a>
    </div>
  );
};

export default Check;
